
import HeaderBar from '../../components/header.vue'
import FooterBar from '../../components/footer.vue'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

const list = () => ['', '', '']

export default {
  components: {
    Tab,
    TabItem,
    Swiper,
    SwiperItem,
    HeaderBar,
    FooterBar,
    Cell,
    CellBox,
    FormPreview
  },
  data() {
    return {
      list2: list(),
      demo2: '',
      index: 0,
      showContent002: false,
      list: [{
        label: '',
        value: ''
      }, {
        label: '',
        value: '101 '
      }, {
        label: '',
        value: '7500'
      }],
      buttons1: [{
        style: 'default',
        text: '',
        onButtonClick: (name) => {
          alert(`clicking ${name}`)
        }
      }, {
        style: 'primary',
        text: '',
        onButtonClick: (name) => {
          alert(`clicking ${name}`)
        }
      }]
    }
  }
}



